const Quote = ({ quote }) => {
  return (
    <div className="card my-3">
      <div className="card-body">
        {quote && (
          <div className="row">
            <div className="col-9">
              <p className="card-text">{quote.content}</p>
            </div>
            <div className="col-3 text-end">
              <h5 className="card-title">{quote.character.firstname}</h5>
              <h6 className="card-subtitle mb-2 text-muted">
                {quote.character.lastname}
              </h6>
            </div>
          </div>
        )}
        {!quote && <div className="text-center text-danger">No Data</div>}
      </div>
    </div>
  );
};
export default Quote;
