import classes from './Jumbotron.module.css';
import logo from '../../assets/the-office-logo.png';

const Jumbotron = (props) => {
  return (
    <div className="row align-items-start rounded-pill bg-dark text-white p-5">
      <div className="col-8 ps-5">
        <h1 className={classes.heading}>DUNDER MIFFLIN</h1>
        <p className="lead">Thats what she said!</p>
      </div>
      <div className="col-4 pe-5">
        <img
          src={logo}
          className="img-thumbnail float-end"
          alt="The Office Logo"
        />
      </div>
    </div>
  );
};

export default Jumbotron;
