import { useReducer } from 'react';
import data from '../../assets/data/quotes-data.json';
import QuotesList from './QuotesList/QuotesList';
import SearchForm from './SearchForm/SearchForm';

const filterQuotes = (quotes, query, defaultSearch) => {
  return quotes.filter((quote) => {
    const searchContent = defaultSearch
      ? quote.character.firstname + ' ' + quote.character.lastname
      : quote.content;
    return searchContent.toLowerCase().includes(query.toLowerCase());
  });
};
const shuffleQuotes = (quotes) => {
  return quotes.sort(() => Math.random() - 0.5);
};
const alphaSortQuotes = (quotes) => {
  return quotes.sort((a, b) => {
    const x = a.character.firstname + ' ' + a.character.lastname;
    const y = b.character.firstname + ' ' + b.character.lastname;
    if (x < y) return -1;
    if (x > y) return 1;
    return 0;
  });
};
const getQuotes = (quotes, order) => {
  if (order === 'shuffle') return shuffleQuotes(quotes);
  if (order === 'alpha') return alphaSortQuotes(quotes);
  if (order === 'ralpha') return alphaSortQuotes(quotes).reverse();
};

const quotesReducer = (state, action) => {
  if (action.type === 'sort') {
    let quotes = filterQuotes(state.data, action.query, state.defaultSearch);
    return {
      ...state,
      quotes: getQuotes(quotes, state.order),
    };
  }
  if (action.type === 'toggle') {
    const newSearchState = !state.defaultSearch;
    const quotes = filterQuotes(state.data, action.query, newSearchState);
    return {
      ...state,
      defaultSearch: newSearchState,
      quotes:
        action.query.length === 0
          ? state.quotes
          : getQuotes(quotes, state.order),
    };
  }
  if (action.type === 'order') {
    return {
      ...state,
      quotes: getQuotes(state.quotes, action.order),
      order: action.order,
    };
  }
};

const quotesdata = shuffleQuotes(data.data);

const Quotes = () => {
  const [state, quotesDispatcher] = useReducer(quotesReducer, {
    data: quotesdata,
    quotes: quotesdata,
    defaultSearch: true,
    order: 'shuffle',
  });

  const onInputChange = (query) => {
    // //uncomment this if needed to shuffel everytime input is cleared
    //if (query.length === 0) return quotesDispatcher({ type: 'shuffle' });
    quotesDispatcher({ type: 'sort', query });
  };
  const onToggleSearch = (query) => {
    quotesDispatcher({ type: 'toggle', query });
  };
  const onChangeOrder = (order) => {
    quotesDispatcher({ type: 'order', order });
  };

  return (
    <>
      <SearchForm
        defaultSearch={state.defaultSearch}
        order={state.order}
        nodata={state.quotes.length === 0}
        onInputChange={onInputChange}
        onToggleSearch={onToggleSearch}
        onChangeOrder={onChangeOrder}
      />
      <QuotesList quotes={state.quotes} />
    </>
  );
};
export default Quotes;
