import { useRef } from 'react';
import classes from './SearchForm.module.css';
import {
  Shuffle,
  SortAlphaDown,
  SortAlphaDownAlt,
} from 'react-bootstrap-icons';
const SearchForm = (props) => {
  const queryInput = useRef();
  const btnShuffle = useRef();
  const btnAlpha = useRef();
  const btnRAlpha = useRef();

  const placeHolder = props.defaultSearch ? 'Character' : 'Quote';
  const btnShuffleDisabled = props.nodata;
  const btnAlphaDisabled = props.nodata || props.order === 'alpha';
  const btnRAlphaDisabled = props.nodata || props.order === 'ralpha';

  const inputChangeHandler = () => {
    props.onInputChange(queryInput.current.value);
  };
  const toggleSearchHandler = () => {
    props.onToggleSearch(queryInput.current.value);
  };
  const changeOrderHandlerShuffle = () => {
    props.onChangeOrder('shuffle');
  };
  const changeOrderHandlerAlpha = () => {
    props.onChangeOrder('alpha');
  };
  const changeOrderHandlerRAlpha = () => {
    props.onChangeOrder('ralpha');
  };
  return (
    <>
      <div className="card rounded-3 text-dark bg-light mb-3">
        <div className="card-header">
          <div className="row">
            <div className="col-6">
              <h4 className="display-4 text-left">Quotes</h4>
            </div>
            <div className="col-5 align-self-center rounded-3">
              <input
                onChange={inputChangeHandler}
                type="text"
                id="character"
                className="form-control"
                placeholder={placeHolder}
                ref={queryInput}
              />
            </div>
            <div className="col-1 align-self-center">
              <div className="form-check form-switch">
                <input
                  className="form-check-input"
                  type="checkbox"
                  id="flexSwitchCheckChecked"
                  checked={props.defaultSearch}
                  onChange={toggleSearchHandler}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={`text-end ${classes['text-end']}`}>
        <button
          ref={btnShuffle}
          onClick={changeOrderHandlerShuffle}
          disabled={btnShuffleDisabled}
          className="btn btn-primary"
        >
          <Shuffle />
        </button>
        <button
          ref={btnAlpha}
          onClick={changeOrderHandlerAlpha}
          disabled={btnAlphaDisabled}
          className="btn btn-primary"
        >
          <SortAlphaDown />
        </button>
        <button
          ref={btnRAlpha}
          onClick={changeOrderHandlerRAlpha}
          disabled={btnRAlphaDisabled}
          className="btn btn-primary"
        >
          <SortAlphaDownAlt />
        </button>
      </div>
    </>
  );
};

export default SearchForm;
